@import 'mixins';

$large-size: 1320px;
$medium-size: 1024px;
$small-size: 768px;
$smallest-size: 480px;

$font-size-base: 14;
$line-heaight-base: 17px;
$font-family-base: 'Roboto', sans-serif;
$font-family-solid: 'Montserrat', sans-serif;
$font-family-header: 'Fugaz One', cursive;
$font-family-text: 'Open Sans', sans-serif;

$primary-1: #ae0032;
$primary-2: #455162;
$primary-3: #1f2f46;

$neutral-1: #0d1720;
$neutral-2: #636363;
$neutral-3: #dadada;
$neutral-4: #f2f2f2;
$neutral-5: #fcfbfa;

$gray-1: #F1F1F1;
$gray-2: #404040;
$gray-3: #292929;
$gray-4: #333C30;
$gray-5: #F8F8F8;
$gray-6: #D4D4D4;

$orange: #EB5B1F;
$orange-1:#B44619;
$orange-hover:#DC6331;

$green-1: #1B3A1B;

@import '~normalize.css/normalize.css';
@import 'fonts';
@import './modules/typography.scss';

html {
  box-sizing: border-box;
  font-size: $font-size-base + 0px;
}

*,
*:after,
*:before {
  box-sizing: inherit;
}

body {
  display: flex;
  flex-direction: column;
  width: 100%;
  min-height: 100vh;
  color: $neutral-1;
  font-weight: normal;
  font-family: $font-family-base;
}

.root {
  flex-grow: 1;
  width: 100%;
  margin-top: 121px;
  @include phone {
    margin-top: 104px;
  }
}

.root-show-menu {
  @include phone {
    &::after {
      content: '';
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      width: 100%;
      height: 100vh;
      z-index: 2;
      background-color: rgba(51, 60, 48, 0.8);
    }
  }
}

.root-hide-menu {
  @include phone {
    background-color: transparent;
  }
}

.container {
  width: $large-size + 30px;
  max-width: 100%;
  margin: 0 auto;
  padding-right: 15px;
  padding-left: 15px;
}

input, select {
  border-radius: 0;
  box-shadow: none;
  -webkit-appearance: none;
}

a,
button {
  outline: none;
  &:focus {
    outline: none;
  }
}

img {
  max-width: 100%;
  height: auto;
}

.btn {
  display: inline-block;
  font-family: $font-family-header;
  padding-top: 20px;
  padding-bottom: 20px;
  font-weight: normal;
  text-transform: uppercase;
  text-decoration: none;
  font-size: 16px;
  line-height: 23px;
  border-radius: 0;
  border-width: 1px;
  border-style: solid;
  @include phone {
    font-size: 14px;
    line-height: 21px;
  }
}

.btn + .btn {
  margin-left: 14px;
  @include phone {
    margin-left: 0;
    margin-top: 10px;
  }
}

.list-reset {
  margin: 0;
  padding: 0;
  list-style: none;
}

.link-reset {
  text-decoration: none;
}

.columns-2 {
  flex-basis: 50%;
  @include phone {
    flex-basis: 100%;
  }
}

.hide_on_medium { 
  @include medium { display: none; }
}

.show_on_medium { 
  @include medium-mf { display: none; }
}

.hide_on_mobile { 
  @include max-screen(380px) { display: none; }
}

.hide_on_small_mobile { 
  @include max-screen(374px) { display: none; }
}

.show_on_mobile_only { 
  @include min-screen(380px) { display: none; }
}

.show_on_small_mobile_only { 
  @include min-screen(374px) { display: none; }
}

// animation

@keyframes show {
  0% {
    transform: translateX(-300px);
  }
  100% {
    transform: translateY(0px);
  }
}